import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Box from '../theme/Box/index';
import Text from '../theme/Text/index';
import Input from '../theme/Input/index';
import Checkbox from '../theme/Checkbox/index';
import Button from '../theme/Button/index';
import Image from '../theme/Image/index';
import RegisterFormWrapper, { FormCol } from './registerForm.style';
import 'rc-tabs/assets/index.css';
import LogoImage from '../../images/logo.jpeg';
import LoginImage from '../../images/login-bg.jpg';
import useForm from '../../hooks/useForm';
import Select from '../theme/Select/index';
import { createUser } from '../../lib/old-firebase';
import LeagueImage from '../LeagueImage/index';
import StepsCollapsible from '../StepsCollapsible';
import { navigate } from 'gatsby';
import { useUserStore } from '../../store/user';

const YAHOO_URL = `https://api.login.yahoo.com/oauth2/request_auth?response_type=code&redirect_uri=${process.env.GATSBY_WEBSITE_URL}&client_id=dj0yJmk9MkNLSmxtTUJMYWhjJmQ9WVdrOVpYUk1hWHBJTXpRbWNHbzlNQS0tJnM9Y29uc3VtZXJzZWNyZXQmc3Y9MCZ4PWVi`;
const YAHOO_KEY = 'yahoo_cache';

const options = [
    {
        value: 'espn',
        label: 'ESPN',
        privateLeagueMessage:
            "Since your league is private we won't be able to get your current data only historic data unless you make your league public or provide us with your espn cookies.",
        requiredIfPrivate: false,
    },
    { value: 'sleeper', label: 'Sleeper' },
    { value: 'yahoo', label: 'Yahoo' },
    { value: 'nfl', label: 'NFL', beta: true, alertLabel: 'nfl.com' },
];

function validateForm({ name, leagueid, email, platform }) {
    if (name === '') {
        return "Name can't be empty";
    } else if (leagueid === '') {
        return "League id can't be empty";
    } else if (!/\d+$/.test(leagueid)) {
        return 'League id must only contain numbers';
    } else if (email === '') {
        return "Email can't be empty";
    }

    return null;
}

const RegisterForm = ({
    row,
    col,
    btnStyle,
    logoStyle,
    checkboxStyle,
    contentWrapper,
    descriptionStyle,
    setAlertText,
}) => {
    const { user } = useUserStore((state) => {
        return {
            user: state.user,
        };
    });

    const [values, setForm, manuallySetForm] = useForm({
        name: user?.name || '',
        leagueid: '',
        email: user?.email || '',
        platform: options[0],
        consolationGames: false,
        cookie: null,
    });

    const [message, setMessage] = useState({ content: 'Please fill out the data below' });
    const [showCookieField, setShowCookieField] = useState(false);
    const [isLoading, setLoading] = useState(false);
    const [hasloaded, setHasLoaded] = useState(false);
    const [hasPrivatelyChecked, setHasPrivateChecked] = useState(false);
    const [canSubmit, setCanSubmit] = useState(false);

    useEffect(() => {
        if (user && !values.email) {
            setForm({ target: { name: 'email', value: user.email } });
        }
    }, [user]);

    useEffect(() => {
        const selectedPlatform = options.findIndex(
            (opt) => values.platform.value === opt.value && opt.beta
        );

        if (selectedPlatform !== -1) {
            setAlertText(`WARNING: ${options[selectedPlatform].alertLabel} is in beta`);
        } else {
            setAlertText('');
        }
    }, [values]);

    useEffect(() => {
        async function fetchYahooData() {
            const queryString = window.location.search;
            const urlParams = new URLSearchParams(queryString);
            const yahoo_token = urlParams.get('code');
            if (!!yahoo_token) {
                setHasLoaded(true);
                setLoading(true);
                try {
                    let userData = JSON.parse(window.localStorage.getItem(YAHOO_KEY));
                    manuallySetForm(userData);
                    userData = { ...userData, platform: userData.platform.value };
                    const yahoo_api_data = await getUserToken(yahoo_token);
                    userData = { ...userData, yahoo_api_data };
                    await setUser(userData);
                } catch (err) {
                    setMessage({ content: err.message });
                } finally {
                    setLoading(false);
                }
            }
            setHasLoaded(true);
        }
        if (typeof window !== 'undefined') {
            fetchYahooData();
        }
    }, []);

    async function handleSubmit(e) {
        setLoading(true);
        e.preventDefault();
        let userData = {
            ...values,
            buildyear: '',
            buildweek: '',
            platform: values.platform.value,
        };

        const error = validateForm(userData);
        if (error === null) {
            await fetch(`${process.env.GATSBY_API_URL}/.netlify/functions/handleRegistration`, {
                method: 'POST',
                body: JSON.stringify({
                    name: userData.name,
                    leagueid: userData.leagueid,
                    email: userData.email,
                    platform: userData.platform,
                }),
            });
            if (userData.platform === 'yahoo') {
                window.localStorage.setItem(
                    YAHOO_KEY,
                    JSON.stringify({ ...values, buildyear: '', buildweek: '' })
                );

                const blankLink = document.createElement('a');
                blankLink.setAttribute('href', YAHOO_URL);
                // blankLink.setAttribute('target', '_blank');
                blankLink.style.display = 'none';
                document.body.appendChild(blankLink);
                blankLink.click();
                document.body.removeChild(blankLink);
            } else {
                await setUser(userData);
            }
        } else {
            console.log(error);
            setMessage({ content: error });
        }
        setLoading(false);
    }

    async function setUser(user) {
        if (!showCookieField) delete user.cookie;

        try {
            await createUser(user);
            setMessage({
                content: `Thank you ${user.name}, your league at ${user.platform} with id ${user.leagueid} was successfully registered!`,
            });

            navigate(`/thank-you?platform=${user.platform}&leagueid=${user.leagueid}`);
        } catch (err) {
            setMessage({
                content: err.message,
            });
        }
    }

    async function getUserToken(yahooToken) {
        if (typeof window !== 'undefined') {
            const request = await fetch(`${process.env.GATSBY_API_URL}/.netlify/functions/index`, {
                method: 'POST',
                body: JSON.stringify({ yahooToken }),
            });

            if (request.status === 200) {
                const yahooApiInfo = await request.json();
                return yahooApiInfo;
            } else {
                throw new Error('Invalid token');
            }
        }
    }

    const [debounced, setDebounced] = React.useState();

    React.useEffect(() => {
        async function checkForPrivateLeague() {
            if (values.platform && values.leagueid) {
                clearTimeout(debounced);
                async function performPrivateCheck() {
                    const req = await fetch(
                        `${process.env.GATSBY_API_URL}/.netlify/functions/leagueCheck`,
                        {
                            method: 'POST',
                            body: JSON.stringify({
                                platform: values.platform.value,
                                leagueId: values.leagueid.trim(),
                                cookie: values.cookie,
                            }),
                        }
                    );

                    if (req.status !== 200) {
                        setShowCookieField(true);
                        const defaultMessage =
                            'Your league seems to be private. Please make it public and try again or send us your cookie.';
                        setMessage({
                            content:
                                options.find((l) => l.value === values.platform.value)
                                    ?.privateLeagueMessage || defaultMessage,
                        });
                    } else {
                        setMessage({ content: '' });
                    }

                    setHasPrivateChecked(true);
                    setCanSubmit(
                        req.status === 200 ||
                            options.find((p) => p.value === values.platform.value)
                                ?.requiredIfPrivate === false
                    );
                }

                let timeout = setTimeout(() => {
                    try {
                        performPrivateCheck();
                    } catch {}
                }, 500);
                setDebounced(timeout);
            }
        }

        try {
            checkForPrivateLeague();
        } catch {}
    }, [values.cookie, values.leagueid, values.platform]);
    return (
        <RegisterFormWrapper>
            <Box className="row" {...row}>
                <Box className="col imageCol h-100" {...col}>
                    <Image className="patternImage" src={LoginImage} alt="Login Banner" />
                </Box>
                <Box className="col tabCol" {...col}>
                    <Box {...contentWrapper}>
                        {hasloaded && (
                            <FormCol>
                                <Image src={LogoImage} {...logoStyle} alt="Logo" />
                                <br></br>
                                <Text content={`${message.content}`} {...descriptionStyle} />

                                <form onSubmit={handleSubmit}>
                                    <h2> League Information </h2>
                                    <Select
                                        name="platform"
                                        labelText="Platform"
                                        options={options}
                                        isSearchable={false}
                                        value={values.platform}
                                        onChange={(e) => {
                                            setShowCookieField(false);
                                            setHasPrivateChecked(false);
                                            setForm({ target: { value: e, name: 'platform' } });
                                        }}
                                    ></Select>
                                    <LeagueImage league={values.platform.value} />
                                    <Input
                                        name="leagueid"
                                        value={values.leagueid.trim()}
                                        onChange={(e) => {
                                            setHasPrivateChecked(false);
                                            setForm(e);
                                        }}
                                        inputType="text"
                                        isMaterial
                                        label="League Id (See image above, please enter just the numbers)"
                                    />
                                    {showCookieField && (
                                        <>
                                            <StepsCollapsible
                                                league={values.platform.value}
                                                label="How to get your cookies?"
                                            />
                                            <br />
                                            <Input
                                                name="cookie"
                                                value={values.cookie}
                                                onChange={(e) => {
                                                    setHasPrivateChecked(false);
                                                    setForm(e);
                                                }}
                                                inputType="text"
                                                isMaterial
                                                label={`Your ${values.platform.value} cookie`}
                                            />
                                        </>
                                    )}
                                    <Checkbox
                                        labelText="Include consolation bracket games in the data? (Beta) "
                                        name="consolationGames"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            manuallySetForm((prev) => {
                                                return {
                                                    ...prev,
                                                    consolationGames: !prev.consolationGames,
                                                };
                                            });
                                        }}
                                        {...checkboxStyle}
                                        isMaterial
                                        isChecked={values.consolationGames}
                                    />
                                    <h2> Contact Information </h2>
                                    <Input
                                        name="name"
                                        value={values.name}
                                        onChange={setForm}
                                        inputType="text"
                                        isMaterial
                                        label="Name"
                                    />
                                    <Input
                                        name="email"
                                        value={values.email}
                                        onChange={setForm}
                                        inputType="email"
                                        isMaterial
                                        label="Email"
                                    />

                                    <div>
                                        <Button
                                            className="default"
                                            type="submit"
                                            title="Submit"
                                            disabled={
                                                isLoading ||
                                                hasPrivatelyChecked === false ||
                                                canSubmit === false
                                            }
                                            isLoading={isLoading}
                                            {...btnStyle}
                                        />
                                    </div>
                                </form>
                            </FormCol>
                        )}
                    </Box>
                </Box>
            </Box>
        </RegisterFormWrapper>
    );
};

RegisterForm.propTypes = {
    row: PropTypes.object,
    col: PropTypes.object,
    logoStyle: PropTypes.object,
    titleStyle: PropTypes.object,
    hintTextStyle: PropTypes.object,
    contentWrapper: PropTypes.object,
    descriptionStyle: PropTypes.object,
    googleButtonStyle: PropTypes.object,
    checkboxStyle: PropTypes.object,
};

// LoginModal default style
RegisterForm.defaultProps = {
    // Team member row default style
    row: {
        flexBox: true,
        flexWrap: 'wrap',
    },
    // Team member col default style
    col: {
        width: [1, 1 / 2],
    },
    // Default logo size
    logoStyle: {
        width: '350px',
        height: 'auto',
        ml: '15px',
    },
    // Title default style
    titleStyle: {
        fontSize: ['22px', '36px', '50px'],
        fontWeight: '400',
        color: '#20201D',
        letterSpacing: '-0.025em',
        mt: '35px',
        mb: '10px',
    },
    // Description default style
    descriptionStyle: {
        color: 'red',
        fontSize: '16px',
        lineHeight: '26px',
        letterSpacing: '-0.025em',
        mb: '23px',
        ml: '1px',
    },
    // Content wrapper style
    contentWrapper: {
        pt: ['32px', '56px'],
        pl: ['17px', '32px', '38px', '40px', '56px'],
        pr: '32px',
        pb: ['32px', '56px'],
    },
    // Default button style
    btnStyle: {
        minWidth: '156px',
        fontSize: '14px',
        fontWeight: '500',
    },
    // Outline button outline style
    outlineBtnStyle: {
        minWidth: '156px',
        fontSize: '14px',
        fontWeight: '500',
        color: 'rgb(26, 115, 232)',
    },
    // Google button style
    googleButtonStyle: {
        bg: '#ffffff',
        color: '#343D48',
    },
    //Checkbox style
    checkboxStyle: {
        mb: '23px',
    },
};

export default RegisterForm;
