import React from 'react';
import ESPNImage from '../../images/leagues/espn.png';
import YahooImage from '../../images/leagues/yahoo.png';
import SleeperImage from '../../images/leagues/sleeper.png';
import { LeagueImg } from './LeagueImage.style';

const LeagueImage = ({ league }) => {
    let src = null;
    switch (league.toLowerCase()) {
        case 'yahoo':
            src = YahooImage;
            break;
        case 'espn':
            src = ESPNImage;
            break;
        case 'sleeper':
            src = SleeperImage;
            break;
        default:
            return null;
    }

    return <LeagueImg src={src} alt={`Find the ${league} id at the end of its url.`} />;
};

export default LeagueImage;
