import { getFirebase } from './firebase'

export async function createUser(user) {
    const firebase = getFirebase();
    const userSlug = `${user.platform}-${user.leagueid}`;
    const testDoc = await firebase.firestore().collection('users').doc(userSlug).get();

    if (testDoc.exists === false) {
        try {
            await firebase
                .firestore()
                .collection('users')
                .doc(userSlug)
                .set({ ...user, createdAt: firebase.firestore.Timestamp.fromDate(new Date()) });
        } catch {
            throw new Error('There was an error. Please check the data and try again.');
        }
    } else {
        throw new Error(
            `League at ${user.platform} with id ${user.leagueid} already registered. If this is a mistake send an email to fantasy@squidsource.com`
        );
    }
}
